.responsive {
	background: url(img/BG-Azul.png) 73% 0% no-repeat;
	background-size: 94%;
	h1 {
		color: $color-Blanco;
	}
	p:first-of-type {
		margin-top: 50px;
		margin-bottom: 68px;
	}
	p {
		color: $color-Blanco;
		&.Items {
			@extend %Text-Mayuscula;
			margin: 22px 0;
			span {
				color: $color-Celeste;
				background-color: $color-Blanco;
				padding: 0px 12px;
			}
		}
	}
	.cuadrado {
		background-color: $color-Celeste;
		width: 155px;
		height: 65px;
		position: absolute;
		right: 14%;
		margin-top: -26px;
	}
	.Padding-Diseño {
		padding-top: 205px;
		z-index: 35;
	}
	.Line5Col {
		position: absolute;
		margin-top: 306px;
		padding-left: 0;
		padding-right: 0;
		z-index: 29;
	}
	.BG-Phone {
		background-image: url("../img/Mockup2-0.gif");
		height: 892px;
		background-position: 50% 0%;
		background-size: 100%;
		background-repeat: no-repeat;
	}
	#Line2-SVG {
		position: absolute;
		z-index: 25;
	}
	#responsive_misela_cuadraditos_01 {
		margin-top: 11px;
	}
	#responsive_misela_cuadraditos_02 {
		margin-top: 209px;
		left: 40px;
		position: absolute;
	}
	.Mockup2 {
		-webkit-filter: drop-shadow(0px 0px 12px rgba(0, 0, 0, 0.58));
		filter: drop-shadow(0px 0px 12px rgba(0, 0, 0, 0.58));
		z-index: 60;
		top: 45px;
	}
	.Mockup2 img {
		max-width: 80%;
	}
	@media (max-width: 1620px) {
		.hidder-Responsive {
			display: none;
		}
		.Mockup2 img {
			max-width: 100%;
		}
	}
	@media (max-width: 1360px) {
		.Padding-Diseño {
			padding-top: 170px;
		}
		.Line5Col {
			margin-top: 238px;
		}
		#responsive_misela_cuadraditos_02 {
			margin-top: 162px;
		}
		.cuadrado {
			margin-top: -12px;
		}
	}
	@media (max-width: 992px) {
		background-repeat: repeat;
		background-size: 100%;
		.cuadrado {
			display: none;
		}
		#responsive_misela_cuadraditos_02 {
			margin-top: 122px;
		}
		p:first-of-type {
			margin-bottom: 46px;
		}
	}
	@media (max-width: 767px) {
		.Padding-Diseño {
			padding-top: 30px;
		}
	}
}